@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: rgb(105, 105, 105);

}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar h1 {
  color: #f1356d;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar a:hover {
  color: #f1356d;
}

.content {
  max-width: 1200px;
  margin: 40px auto;
  padding: 20px;
  text-align: center;

}

.search-bar {
  margin: 0 auto;
  text-align: center;
}

/*product list and etc*/
.product-list {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;


}

.product-preview {
  width: 100%;
  max-width: 250px;
  margin: 1em 1em;
}

.product-preview a {
  text-decoration: none;
  color: inherit;
}

.product-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

/*  product details page */
.product-details {
  max-width: 800px;
  margin: 0 auto;
}

.product-details h2 {
  font-size: 200%;
  color: #f1356d;
  margin-bottom: 10px;
}

.product-details div {
  margin: 5px 0;
}

.product-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 2%;
  border-radius: 8px;
  cursor: pointer;
  font-size: 100%;
}

.add-cart {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
}

.button-counter {
  background-color: white;
  color: black;
}

.sign-in-page button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1.5em;
  width: 100%;
}

/* create new blog form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.create label {
  text-align: left;
  display: block;
}

.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}

.create-input input,
.create-input textarea,
.create-input select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}


.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.button-class {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
/*Desktop View*/
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    height: 80px;
    margin: 0 auto;
    max-width: 800px;
    border-bottom: 1px solid #f2f2f2;

}

.header h1 {
    color: inherit;

}

.logo-nav {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo {
    width: 45px;
    height: 45px;
}

.nav-options {
    margin: 0;
    padding: 0;
    overflow: hidden;
    list-style: none;
}

.nav-options li {
    float: left;
    padding: 16px;
}

.mobile-option {
    display: none;
}

.option :hover {
    color: #f1356d;
}

.signin-up {
    display: flex;
    padding: 0px 5px;
    list-style-type: none;
}

.sign-in {
    padding-right: 50px;
    align-self: center;
}

.sign-in :hover {
    color: #f1356d;
}

.signup-btn {
    padding: 10px 10px;
    height: 2.4rem;
    border-radius: 3px;
    background: #f1356d;
    color: white;
    cursor: pointer;
    align-items: center;
}

.mobile-menu {
    display: none;
}

@media (max-width: 648px) {

    /*Mobile View */
    .header {
        padding: 0px 10px;
    }

    .logo {
        width: 45px;
        height: 45px;
    }

    .nav-options {
        display: flex;
        width: 100%;
        height: 350px;
        position: absolute;
        top: 80px;
        left: -100%;
        transition: all 0.5s ease;
        flex-direction: column;
        list-style-type: none;
        grid-gap: 0px;
    }

    .nav-options.active {
        background: rgba(255, 255, 255, 1);
        left: 0;
        transition: all 0.5s ease;
        z-index: 2;
        align-content: center;
        padding-left: 0px;
    }

    .menu-icon {
        width: 45px;
        height: 45px;
    }

    .option {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 10vw;
        padding: 30px 0px;
    }

    .sign-up {

        border-radius: 3px;
        background: #f1356d;
        color: white;
        padding: 20px 0px;
        width: 80%;
        align-self: center;
        border-radius: 8px;
    }

    .signin-up {
        display: none;
    }
}

.mobile-menu {
    display: block;
}
